import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import ActionButtonArrow from '@webui-one/components/ConfigRender/List/static/ActionButtonArrow';
import useSafeOnClick from '@webui-one/components/hooks/useSafeOnClick';
import { ACTION_STATUS } from './selectors';
import './ActionButton.css';

const loaderStyle = {
    width: '3rem',
    margin: '0 auto',
    transform: 'scale(0.5)',
};

const executeStyle = {
    display: 'block',
    flexGrow: 1,
    paddingRight: '2.275rem',
};

const expandStyle = {
    position: 'absolute',
    right: '0',
    height: '100%',
    padding: '0 0.75rem 0.5rem 0.5rem',
    borderLeft: `1px solid ${defaultStyles.color3}66`, // 40% opacity
};

const expandStyle2 = {
    ...expandStyle,
    width: '100%',
    padding: '0.5rem 0 0 0',
};

const applyButtonStyle = (result, style, timeout, completedAt) => {
    if (completedAt) {
        return {
            ...style,
            backgroundColor: result.color,
            animationName: 'actionButtonAnimation',
            animationDuration: `${timeout}s`,
            animationFillMode: 'forwards',
            animationTimingFunction: 'ease-out',
            animationDelay: `-${(Date.now() - completedAt) * 0.001}s`,
        };
    }

    return {
        ...style,
        backgroundColor: result.color,
    };
};

const applyActiveExpandStyle = (style, isExpanded) => {
    if (isExpanded) {
        return {
            ...style,
            backgroundColor: defaultStyles.color20,
            animationName: 'none',
            borderRadius: '1rem 1rem 0 0',
            boxShadow: `${defaultStyles.color20} 0 0.325rem 0px 0px`,
        };
    }
    return style;
};

const ActionButton = ({
    path,
    result,
    onAction,
    action,
    style,
    isExpanded,
    onExpand,
    status,
    actionIndicatorTimeout,
    completedAt,
    isDisabled,
}) => {
    const onActionFactory = () => (() => (onAction(action, path)));
    const onClick = useSafeOnClick(onActionFactory());

    let content = action;

    if (result === ACTION_STATUS.RESULT_PENDING) {
        content = (
            <div style={loaderStyle}>
                <Loader color={defaultStyles.color11} />
            </div>
        );
    }
    const hasResult = result !== ACTION_STATUS.RESULT_NA &&
                        result !== ACTION_STATUS.RESULT_PENDING;
    const buttonStyle = status !== undefined ?
        applyButtonStyle(result, style, actionIndicatorTimeout, completedAt) :
        applyButtonStyle(ACTION_STATUS.RESULT_UNKNOWN, style, null, null);

    return (
        <div
            data-handle={`action-${action}-${status}`}
            style={applyActiveExpandStyle(buttonStyle, isExpanded)}
            className={style.ActionButton}
        >
            <a
                role="button"
                tabIndex={0}
                data-handle="execute"
                onClick={isDisabled === false ? onClick : () => null}
                style={isExpanded ? { display: 'none' } : executeStyle}
            >
                {content}
            </a>
            {hasResult && (
                <a
                    role="button"
                    tabIndex={0}
                    data-handle="expand"
                    onClick={() => onExpand(path, action)}
                    style={isExpanded ? expandStyle2 : expandStyle}
                >
                    <div style={{
                        transform: isExpanded || 'rotate(180deg)',
                        width: '0.75rem',
                        margin: '0 auto',
                     }}
                    >
                        <ActionButtonArrow color={defaultStyles.color3} />
                    </div>
                </a>
            )}
        </div>

    );
};

ActionButton.propTypes = {
    onAction: PropTypes.func,
    onExpand: PropTypes.func,
    isExpanded: PropTypes.bool,
    isDisabled: PropTypes.bool,
    path: PropTypes.string,
    action: PropTypes.string,
    result: PropTypes.shape({ message: PropTypes.string, color: PropTypes.string }),
    style: PropTypes.shape({ ActionButton: PropTypes.shape({}) }),
    status: PropTypes.string,
    actionIndicatorTimeout: PropTypes.string,
    completedAt: PropTypes.number,
};

ActionButton.defaultProps = {
    isDisabled: false,
};

export default ActionButton;
