import React from 'react';
import PropTypes from 'prop-types';

import ActionSymbol from '@webui-one/styles/static/ActionSymbol';
import ToolTip from '@webui-one/components/ToolTip/ToolTip';
import ModalConfirm from '@webui-one/components/Modal/ModalConfirm';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import modalStyles from '@webui-one/components/Modal/ModalStyles';

import Downarrow from '@webui-one/styles/static/Downarrow';
import style from './BatchActionsStyle';

class BatchActions extends React.Component {
    constructor(props) {
        super(props);
        this.runAction = this.runAction.bind(this);
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
        this.showMenu = this.showMenu.bind(this);
        this.hideMenu = this.hideMenu.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.state = {
            shouldShowMenu: false,
            currentAction: null,
        };
    }

    onKeyDown(e) {
        if (e.keyCode === 27) this.hideMenu();
    }

    runAction(actionName) {
        const { rows, onAction } = this.props;
        rows.forEach(r => onAction(actionName, r.rowKey));
    }

    showTooltip() {
        this.tooltip.show();
    }

    hideTooltip() {
        this.tooltip.hide();
    }

    showMenu() {
        const { rows } = this.props;
        if (rows.length > 0) this.setState({ shouldShowMenu: true });
    }

    hideMenu() {
        this.setState({ shouldShowMenu: false });
    }

    openModal(actionName) {
        this.setState({ currentAction: actionName });
    }

    closeModal() {
        this.setState({ currentAction: null, shouldShowMenu: false });
    }

    render() {
        const { shouldShowMenu, currentAction } = this.state;
        const { rows, actions } = this.props;
        const content = [];
        actions.forEach(actionName => (
            content.push(
                <a
                    role="button"
                    style={style.row}
                    data-handle={`batch-action-${actionName}-test`}
                    key={actionName}
                    onClick={() => this.openModal(actionName)}
                    tabIndex="0"
                >
                    <div style={style.row.wrapper}>
                        <div style={style.row.value}>{actionName}</div>
                        <div style={style.row.img}>
                            <ActionSymbol color={defaultStyles.color1} />
                        </div>
                    </div>
                </a>)
        ),
        );
        const color = rows.length === 0 ? defaultStyles.color6 : defaultStyles.color1;
        return (
            <div style={style.container}>
                <ModalConfirm
                    shouldRender={!!currentAction}
                    formIsValid
                    onConfirm={() => this.runAction(currentAction)}
                    onRequestClose={this.closeModal}
                >
                    <div style={modalStyles.header}>
                        {`Sure you want to execute ${currentAction} on ${rows.length} items?`}
                    </div>
                </ModalConfirm>
                <button
                    type="button"
                    data-handle="batch-actions-button"
                    style={style.button}
                    disabled={false
                        /* if button is disabled, tooltip does not work due to bug in chrome) */
                    }
                    onClick={this.showMenu}
                    onKeyDown={this.onKeyDown}
                    onMouseDown={this.showTooltip}
                    onMouseUp={this.hideTooltip}
                    onMouseEnter={this.showTooltip}
                    onMouseLeave={this.hideTooltip}
                    ref={(mb) => { this.menubutton = mb; }}
                >
                    <div style={style.symbol}><ActionSymbol color={color} /></div>
                    <div style={style.arrow}><Downarrow color={color} /></div>
                </button>
                <ToolTip
                    direction="left"
                    toolTipText="run action"
                    ref={(tt) => { this.tooltip = tt; }}
                />
                {shouldShowMenu && (
                    <div style={style.pop}>
                        <button onClick={this.hideMenu} style={style.overlay} type="button" />
                        <div style={style.menuArrowShadow} />
                        <div
                            data-handle="batch-actions-menu"
                            style={style.wrapper}
                        >
                            <div style={style.header}>Actions:</div>
                            {content}
                        </div>
                        <div style={style.menuArrow} />
                    </div>
                )}
            </div>
        );
    }
}

BatchActions.propTypes = {
    rows: PropTypes.arrayOf(Object),
    onAction: PropTypes.func,
    actions: PropTypes.arrayOf(String),

};

BatchActions.defaultProps = {
    actionName: '',
    actions: [],
};

export default BatchActions;
