import React from 'react';
import PropTypes from 'prop-types';
import style from './DiffTableStyles';


const rowPropTypes = {
    lineNum: PropTypes.number,
    text: PropTypes.string,
};

const PlusRow = ({ lineNum, text }) => (
    <tr style={style.tr}>
        <td style={style.leftColNumber}><div>{lineNum}</div></td>
        <td style={style.leftCol} />
        <td style={style.rightColAdd}><div>{text}</div></td>
    </tr>
);
PlusRow.propTypes = rowPropTypes;

const MinusRow = ({ lineNum, text }) => (
    <tr style={style.tr}>
        <td style={style.leftColNumber}><div>{lineNum}</div></td>
        <td style={style.leftColDelete}><div>{text}</div></td>
        <td style={style.rightCol} />
    </tr>
);
MinusRow.propTypes = rowPropTypes;


const BothRow = ({ lineNum, text }) => (
    <tr style={style.tr}>
        <td style={style.leftColNumber}><div>{lineNum}</div></td>
        <td style={style.leftCol}><div>{text}</div></td>
        <td style={style.rightCol}><div>{text}</div></td>
    </tr>
);
BothRow.propTypes = rowPropTypes;


const renderRow = (row, rowIx) => {
    const lineNum = rowIx + 1;
    if (row.length > 0 && row[0] === '+') {
        return <PlusRow text={row} lineNum={lineNum} key={rowIx} />;
    }
    if (row.length > 0 && row[0] === '-') {
        return <MinusRow text={row} lineNum={lineNum} key={rowIx} />;
    }
    return <BothRow text={row} lineNum={lineNum} key={rowIx} />;
};


const DiffTable = ({ data }) => {
    const rows = data.split('\n').map(renderRow);
    return (
        <table style={style.table}>
            <tbody>
                {rows}
            </tbody>
        </table>
    );
};

DiffTable.propTypes = {
    data: PropTypes.string,
};

export default DiffTable;
export {
    renderRow as _renderRow,
    BothRow as _BothRow,
    MinusRow as _MinusRow,
    PlusRow as _PlusRow,
};
